}

.about-text {
    width: 550px;
    min-width: 30%;
    max-width: 100%;
    padding: 0px 10px;
}

.main {
    width: 75%;
    max-width: 95%;
    margin: 0 auto;
    margin-top: 80px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

.about-text h1 {
    color: #176024d2;
    font-size: 4rem;
    margin-bottom: 20px;
    text-transform: capitalize;
    text-align: center;
}

.about-text h5 {
    color: white;
    font-size: 2rem;
    margin-bottom: 25px;
    text-transform: capitalize;
    letter-spacing: 2px;
    text-align: center;
}

.about-text p {
    color: #c0c4d6;
    font-size: 1rem;
    line-height: 28px;
    /* margin-bottom: 40px; */
    letter-spacing: 2px;
    /* text-align:  center; */
}

@media screen and (max-width:1300px) {
    .about-me {
        width: 100%;
        height: auto;
        padding: 70px 0px;
    }
}

@media screen and (max-width:650px) {
    .navbar-link{
        font-size: 0.9rem !important;
    }
    .vvd{
        padding: 3px 5px !important;
    }
    .social-icon a{
        height: 34px !important;
        width: 34px !important;
    }
    /* background-color: #ffcc74; */
    #basic-navbar-nav{
        -webkit-transform: translate(-5%,0%);
                transform: translate(-5%,0%);
        font-size: 1rem;
    }
    .main img {
        width: 100%;
        margin-bottom: 35px;
        /* margin-top: 80px; */
    }
    .nav-about{
        position: relative;
        left: 0px !important;
    }

    .about-text h1 {
        font-size: 2rem;
        margin-bottom: 25px;
    }
    .about-text h5 {
        font-size: 1.4rem;
        margin-bottom: 25px;
    }
    .about-text p {
        font-size: 0.8rem;
        /* margin-bottom: 25px; */
    }
}

